<template>
	<div>
		<div class="checklist-title"></div>
		<div class="checklist-title">:{{value1}}</div>
		<w-checkList :options="options1" v-model="value1" :align="align" @on-change="checkListChange"></w-checkList>
		<div class="button-group">
			<w-button type='primary' @click="selectHandle('value1',[''])"></w-button>
			<w-button type='primary' @click="selectHandle('value1',['','',''])"></w-button>
			<w-button type='primary'@click="align = align === 'left'?'right':'left'"></w-button>
		</div>
		<div class="checklist-title">raido</div>
		<div class="checklist-title">:{{value}}</div>
		<w-checkList :options="options" v-model="value" :max="1" align="right" @on-change="checkListChange"></w-checkList>
		<div class="checklist-title">4,2</div>
		<div class="checklist-title">:{{value2}}</div>
		<w-checkList :options="options2" v-model="value2" :max="4" :min="2" align="left" @on-change="checkListChange" ref="checkListObj"></w-checkList>
		<div class="button-group">
			<w-button type='primary' @click="getCheckedOptions"></w-button>
		</div>
	</div>
</template>

<script>

export default {
	name: 'ChecListDemo',
	data () {
		return {
			align: 'right',
			options: [
				{
					value: 'itemValue1',
					name: '1',
					label: '1'
				}, {
					value: 'itemValue2',
					name: '2'
				}, {
					value: 'itemValue3',
					name: '3',
					label: '2'
				}, {
					value: 'itemValue4',
					name: '4',
					disabled: true
				}
			],
			value: ['itemValue2'],
			options1: ['', '', ''],
			value1: [''],
			options2: [
				{
					value: '',
					name: ''
				}, {
					value: '',
					name: ''
				}, {
					value: '',
					name: ''
				}, {
					value: '',
					name: '',
					disabled: false
				}, {
					value: '',
					name: '',
					disabled: false
				}
			],
			value2: [],
			test: ''
		}
	},
	created () {
	},
	methods: {
		selectHandle (value, newVal) {
			this[value] = newVal
		},
		checkListChange (currentValue) {
			console.log('checkListChange', currentValue)
		},
		getCheckedOptions () {
			var obj = this.$refs.checkListObj.getCheckedOptions()
			console.log(obj)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.checklist-title {
	text-align: left;
	padding-left:10px;
	padding-right:10px;
}
.button-group {
	padding-left:10px;
	padding-right:10px;
	margin-top:10px;
	margin-bottom:10px;
}
</style>



